<template>
  <div>
    <b-collapse id="formCollapse">
      <b-form @submit.prevent="processForm">
        <h4 class="mt-4 mb-3">Customer information</h4>
        <b-row class="mb-3">
          <b-col>
            <label for="country" class="form-label">Country</label>
            <b-form-input v-model="country" id="country"></b-form-input>
          </b-col>
          <b-col>
            <label for="externalId" class="form-label">External Id</label>
            <b-form-input v-model="externalId" id="externalId"></b-form-input>
          </b-col>
          <b-col>
            <label for="name" class="form-label">Name</label>
            <b-form-input v-model="name" id="name"></b-form-input>
          </b-col>
        </b-row>
        <div class="d-grid gap-2 d-md-flex justify-content-md-end">
          <b-button
            variant="outline-danger"
            class="actionButton"
            v-b-toggle:formCollapse
            @click="reset()"
            ><i class="far fa-times-circle mr-1"></i>Cancel</b-button
          >
          <b-button
            variant="outline-success"
            type="submit"
            class="actionButton"
            v-b-toggle:formCollapse
            ><i class="far fa-check-circle mr-1"></i
            >{{ txtBtnFormulario }}</b-button
          >
        </div>
      </b-form>
    </b-collapse>
  </div>
</template>

<script>
export default {
  name: 'Customer',
  data() {
    return {
      // Customer fields
      id: '',
      country: '',
      externalId: '',
      name: '',
      //-----------
      perPage: 10,
      currentPage: 1,
      txtBtnFormulario: 'Save',
      editState: false
    };
  },
  methods: {
    processForm() {
      if (this.editState == false) {
        var dataSave = {
          country: this.country,
          externalId: this.externalId,
          name: this.name
        };

        this.$parent.saveData(dataSave);
      } else {
        var dataUpdate = {
          id: this.id,
          country: this.country,
          externalId: this.externalId,
          name: this.name
        };

        this.$parent.updateData(dataUpdate);
      }

      this.reset();
    },
    reset() {
      this.id = '';
      this.country = '';
      this.externalId = '';
      this.name = '';

      this.txtBtnFormulario = 'Save';
      this.editState = false;
    },
    loadItem(item) {
      this.id = item.id;
      this.country = item.country;
      this.externalId = item.externalId;
      this.name = item.name;
      this.txtBtnFormulario = 'Update';
      this.editState = true;
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
  },
  components: {}
};
</script>
